import Vue from "vue"
import VueRouter from "vue-router"
import storageUtil from "../assets/script/util/storage"
import user from "../views/user/script/router"
import picture from "../views/picture/script/router"
import authorizeCode from "../views/authorizeCode/script/router"
Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "content",
    redirect: "/user",
    component: () => import("../views/Content"),
    children: [...user, ...picture, ...authorizeCode]
  },
  {
    path: "/authorize",
    name: "authorize",
    component: () => import("../views/Authorize")
  }
]

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (
    (storageUtil.sessionGet("accessKey") === null ||
      storageUtil.sessionGet("secretKey") === null) &&
    to.path !== "/authorize"
  ) {
    next("/authorize")
    return
  }
  next() // 必须使用 next ,执行效果依赖 next 方法的调用参数
})

export default router
