import Vue from "vue"
import Vuex from "vuex"
import httpUtil from "../assets/script/util/http"
Vue.use(Vuex)

export default {
  namespaced: true,
  state: {},
  mutations: {},
  actions: {
    /**
     * 注册
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    ASignUp(context, params) {
      return httpUtil.post("/user/signUp", params)
    },
    /**
     * 获取分页列表
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    APaging(context, params) {
      let _params = Object.assign({}, params)
      _params.page--
      return httpUtil.get("/user/paging", _params)
    },
    /**
     * 获取列表
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    AList(context, params) {
      return httpUtil.get("/user/list", params)
    }
  }
}
