import Vue from "vue"
import Vuex from "vuex"
import httpUtil from "../assets/script/util/http"
Vue.use(Vuex)

export default {
  namespaced: true,
  state: {},
  mutations: {},
  actions: {
    /**
     * 获取分页列表
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    APaging(context, params) {
      let _params = Object.assign({}, params)
      _params.startDate = _params.date[0] ? new Date(_params.date[0]) : null
      _params.endDate = _params.date[1] ? new Date(_params.date[1]) : null
      delete _params.date
      _params.page--
      return httpUtil.get("/picture/paging", _params)
    },
    AUpdatePrivacy(context, params) {
      return httpUtil.post("/picture/updatePrivacy", params)
    },
    ABatchRemove(context, params) {
      return httpUtil.post("/picture/batchRemove", params)
    },
    /**
     * 还原
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    AReduction(context, params) {
      return httpUtil.post("/picture/reduction", params)
    },
    ABatchDelete(context, params) {
      return httpUtil.post("/picture/batchDelete", params)
    },
    // 初始化图片
    AInit(context, params) {
      return httpUtil.post("/picture/init", params)
    },
    /**
     * 绑定用户
     * @param context
     * @param params
     * @returns {*|Promise}
     * @constructor
     */
    ABindUser(context, params) {
      return httpUtil.post("/picture/bindUser", params)
    },
    // 建立索引
    AInitIndex() {
      return httpUtil.post("/picture/initIndex")
    },
    // 导入es
    AImportES() {
      return httpUtil.post("/picture/importES")
    },
    AListBySuit(context, params) {
      return httpUtil.get("/collect/listPictureBySuit", params)
    },
    AGiveUpError(context, params) {
      return httpUtil.post("/collect/giveUpError", params)
    }
  }
}
