const PrivacyState = {
  PUBLIC: "公开",
  PRIVATE: "隐藏"
}
const PictureLifeState = {
  EXIST: "正常",
  DISAPPEAR: "不存在"
}

export default {
  PrivacyState,
  PictureLifeState,
  equal(key, enumType, enumValue) {
    return enumType[key] === enumValue
  },
  getKey(enumType, enumValue) {
    for (let keys of Object.keys(enumType)) {
      if (enumType[keys] === enumValue) {
        return keys
      }
    }
  }
}
