import Vue from "vue"
import App from "./App.vue"
import router from "./router"
import store from "./store"
import ElementUI from "element-ui"
import VueLazyload from "vue-lazyload"
import "element-ui/lib/theme-chalk/index.css"
import "./assets/style/index.less"
import "./assets/style/el.less"
import Img from "./assets/script/util/imgUrl"
import filters from "./assets/script/filter"
import _enum from "./assets/script/constant/enum"
import config from "./assets/script/constant/config"
Vue.use(ElementUI)
Vue.config.productionTip = false
Vue.prototype.$img = Img
Vue.prototype.$config = config
Vue.prototype.$filter = filters
Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key])
})
Vue.prototype.$enum = _enum

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require("./assets/image/svg/default-picture.svg"),
  loading: require("./assets/image/svg/default-picture.svg"),
  attempt: 1
})
window.app = new Vue({
  router,
  store,
  render: (h) => h(App)
}).$mount("#app")
