import Vue from "vue"
import Vuex from "vuex"
import httpUtil from "../assets/script/util/http"
Vue.use(Vuex)

export default {
  namespaced: true,
  state: {},
  mutations: {},
  actions: {
    AList() {
      return httpUtil.get("/authorizeCode/list")
    },
    ASave(context, params) {
      return httpUtil.post("/authorizeCode/save", params)
    },
    ARemove(context, params) {
      return httpUtil.post("/authorizeCode/remove", params)
    }
  }
}
